* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.container {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  padding: 20px;
    /* justify-content: space-between;   */
}

.header {
  width: 100%;
  background: #2D2D2D;
  text-align: center;
  size: 40px;
  color: white;
  padding: 30px;
}

.input-component {
  -ms-flex-pack: justify;
      justify-content: space-between;
  background: rgba(150, 135, 135, 0.27);
}

.half {
  -ms-flex: 4 1 0%;
      flex: 4 1 0%;
}

.five {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
}
.wrap {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.card {
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  width: 24%;
  padding-left: 10px;
   border: 2px solid white;
  margin-bottom: 20px;
}

.card:hover {
  border: 2px solid olivedrab;
}

.card:hover .card__hidden {
  visibility: visible;
}

.image {
  border: 2px solid rgba(150, 135, 135, 0.27);
  border-radius: 50%;
}

.card__name {
  color: olivedrab;
}
.card__hidden {
  visibility: hidden;
}


